<template>
	<div class="contact">
		<section id="map" style="width:100%;height:100vh;margin: auto;"></section>
	</div>
</template>


<script type="text/javascript">
	export default{
		mounted(){
		    function initMap(){
		      createMap();//创建地图
		      setMapEvent();//设置地图事件
		      addMapControl();//向地图添加控件
		      addMapOverlay();//向地图添加覆盖物
		    }
		    function createMap(){ 
		      map = new BMap.Map("map"); 
		      map.centerAndZoom(new BMap.Point(104.059126,30.540708),17);
		    }
		    function setMapEvent(){
		      map.enableScrollWheelZoom();
		      map.enableKeyboard();
		      map.enableDragging();
		      map.enableDoubleClickZoom()
		    }
		    function addClickHandler(target,window){
		      target.addEventListener("click",function(){
		        target.openInfoWindow(window);
		      });
		    }
		    function addMapOverlay(){
		      var markers = [
		        {content:`<h1 style="color:#3db194;line-height:2;font-size:14px;">美城悦荣府</h1><p style="font-size:12px;line-height:2;">地址： 四川省成都市双流区丽景路</p>`,title:"",imageOffset: {width:-46,height:-21},position:{lat:30.541233,lng:104.058758}}
		      ];
		      for(var index = 0; index < markers.length; index++ ){
		        var point = new BMap.Point(markers[index].position.lng,markers[index].position.lat);
		        var marker = new BMap.Marker(point,{icon:new BMap.Icon("https://i.loli.net/2019/11/21/PEDgHVAWTrKZXIt.png",new BMap.Size(118,65),{
		          imageOffset: new BMap.Size(markers[index].imageOffset.width,markers[index].imageOffset.height)
		        })});
		        var label = new BMap.Label(markers[index].title,{offset: new BMap.Size(25,5)});
		        var opts = {
		          width: 200,
		          title: markers[index].title,
		          enableMessage: false
		        };
		        var infoWindow = new BMap.InfoWindow(markers[index].content,opts);
		        marker.setLabel(label);
		        addClickHandler(marker,infoWindow);
		        map.addOverlay(marker);
		      };
		      var plOpts = [
		        {strokeColor:"#f00",strokeWeight:"4",strokeOpacity:"0.6"}
		      ];
		      var plPath = [
		        [
		          new BMap.Point(104.059158,30.54259),
		          new BMap.Point(104.059153,30.544161),
		          new BMap.Point(104.063164,30.54413),
		          new BMap.Point(104.068675,30.544118),
		          new BMap.Point(104.06934,30.544052),
		          new BMap.Point(104.070027,30.543776),
		          new BMap.Point(104.0704,30.543523),
		          new BMap.Point(104.072686,30.542019),
		          new BMap.Point(104.073203,30.541723),
		          new BMap.Point(104.073795,30.541572),
		          new BMap.Point(104.074388,30.541556),
		          new BMap.Point(104.075673,30.541587),
		          new BMap.Point(104.07565,30.541587),
		          new BMap.Point(104.075673,30.541587),
		          new BMap.Point(104.075664,30.541587)
		        ],
		      ];
		      for(var index = 0; index < plOpts.length; index++){
		        var polyline = new BMap.Polyline(plPath[index],plOpts[index]);
		        map.addOverlay(polyline);
		      }
		    }
		    //向地图添加控件
		    function addMapControl(){
		      var scaleControl = new BMap.ScaleControl({anchor:BMAP_ANCHOR_BOTTOM_LEFT});
		      scaleControl.setUnit(BMAP_UNIT_IMPERIAL);
		      map.addControl(scaleControl);
		      var navControl = new BMap.NavigationControl({anchor:BMAP_ANCHOR_TOP_LEFT,type:0});
		      map.addControl(navControl);
		    }
		    var map;
		      initMap();
				}
	}
</script>


<style type="text/css">
	.BMap_Marker{
		margin-top:10px !important;
	}

	.BMap_Marker div img{
		margin: 0!important;
	}

	.BMap_Marker  .BMapLabel{
		display: none !important;
	}

	.BMap_pop div img {
		display: none !important;
	}

	.BMap_pop div div{
		border:none !important;
	} 

	.BMap_pop .BMap_top{
		border:none !important;
	} 

	.BMap_pop .BMap_bottom{
		border:none !important;
	}

	.BMap_pop .BMap_center{
		border:none !important;
	} 


	.BMap_shadow div img {
		display: none !important;
	}
</style>